import './donate.html';
import './styles/donate.scss'

const burgerMenuBtn = document.querySelector('.burger-menu-btn');
const burgerMenu = document.querySelector('.burger-menu');

function openBurgerMenu() {
    burgerMenu.classList.remove('burger-menu--inactive');
} 
burgerMenuBtn.addEventListener('click', openBurgerMenu);

const closeBtn = document.querySelector('.close-menu-btn');
const popUpBg = document.querySelector('.popup__background');
function closeBurgerMenu(event) {
    if (event.target === closeBtn || 
        event.target === popUpBg ||
        event.target.innerText === 'Donate'|| 
        event.type === 'scroll') burgerMenu.classList.add('burger-menu--inactive');    
}
window.addEventListener('click', closeBurgerMenu);
window.addEventListener('scroll', closeBurgerMenu);

const dots = document.querySelectorAll('.dot');
dots.forEach(elem => {
    elem.addEventListener('click', chooseAmount);
})

const input = document.querySelector('.input');
function chooseAmount(e) {
    //подвечиваем кружок
    const dot = e.target;
    if (!dot.classList.contains('dot--active')) {
        dots.forEach(elem => elem.classList.remove('dot--active')) 
        dot.classList.add('dot--active');
        
        //подсвечиваем цифры
        let index;
        for (let key in dots) {
            if (dots[key].classList.contains('dot--active')) {
                index = parseInt(key);
                break;
            } 
        }
        const money = document.querySelector('.money').children;
        const moneyArr = [...money];
        moneyArr.forEach(elem => elem.classList.remove('money--active'))
        money[index].classList.add('money--active');
        
        //записываем в инпут выбранную сумму
        const sumNum = money[index].innerText.slice(2);
        input.value = sumNum
    } 
}


window.addEventListener('keyup', () => {
    if(input.value.length > 4) input.value = input.value.slice(0,4);
    
    //проверяем есть ли такая же сумма
    const money = document.querySelector('.money').children;
    const moneyArr = [...money];
    const stringMoneyArr = moneyArr.map(elem => {
        return elem.innerText.slice(2)
    })

    //проверяем инпут и значения
    //если инпута нет в цифрах, то удаляем подсветку со всех элементов
    if (!stringMoneyArr.includes(input.value)) {
        moneyArr.forEach(elem => elem.classList.remove('money--active'))
        dots.forEach(elem => elem.classList.remove('dot--active'));
    } 
    //если инпут есть, то подсвечиваем точку и цифры
    else {
        moneyArr.forEach(elem => {
            if (elem.innerText.slice(2) === input.value) {
                moneyArr.forEach(elem => elem.classList.remove('money--active'))
                elem.classList.add('money--active');
                const index = moneyArr.indexOf(elem);
                dots.forEach(elem => elem.classList.remove('dot--active'));
                dots[index].classList.add('dot--active');
            }
            
        })
    } 
}) 
